* {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

#grid-container {
  display: grid;
  margin: auto;
  margin-top: 2rem;
  width: 70%;
  height: 35rem;
  grid-template-columns: 1fr 4fr;
  grid-template-rows: 5rem;
}

.grid-box {
  background-color: rgb(255, 208, 0);
  border-radius: 1rem;
  text-align: center;
}

#nav {
  margin: 1rem;
}

#delete-link {
  color: red;
}

li {
  list-style: none;
}

h1 {
  color: white;
  font-size: 2rem;
}

h2 {
  font-size: 2rem;
  font-weight: normal;
}

#one {
  grid-column: 1;
  grid-row: 1;
}
#two {
  display: flex;
  grid-column: 2;
  grid-row: 1;
  background-color: white;
  text-align: left;
}
#three {
  grid-column: 1;
  grid-row: 2;
  background-color: white;
}
#four {
  grid-column: 2;
  grid-row: 2;
  background-color: white;
  border: 2px rgb(255, 208, 0) solid;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;

  margin: auto;
  margin-top: auto;
}

#profile-img {
  object-fit: cover;
  height: 5rem;
  width: 5rem;
  margin-left: 0.2rem;
  margin-right: 0.2rem;
}

.register-img {
  width: 5rem;
  margin-left: 0.2rem;
  margin-right: 0.2rem;
}

#header {
  background-color: rgb(255, 208, 0);
  margin: auto;
  width: 70%;
  height: 6rem;
  border-radius: 1rem;
}

#center-circle {
  background-color: rgb(255, 208, 0);
  margin: auto;
  margin-top: 1rem;
  width: 70%;
  height: 35rem;
  border-radius: 1rem;
}

#content-box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;

  margin: auto;
  margin-top: auto;
}

input {
  margin: 0.2rem 0px;
}

#login-form {
  margin: auto;
  display: flex;
  flex-direction: column;
  text-align: center;
}

#register-form {
  margin: auto;
  display: flex;
  flex-direction: column;
  text-align: center;
}

#register-btn {
  width: 6rem;
  align-self: center;
  margin: 1rem;
}

#register-link {
  font-size: 0.8rem;
}

.img-choice {
  visibility: hidden;
}

input:checked + label {
  background: rgb(255, 208, 0);
}

#entry-input {
  margin: 1rem;
  height: 4rem;
  width: 30rem;
}

#entry-box {
  height: 0.5rem;
  display: flex;
  margin: 1rem;
}

#sender-p {
  font-weight: bold;
  margin: 0.2rem;
}

#entry-p {
  margin: 0.2rem;
}

#timestamp-p {
  font-weight: lighter;
  margin: 0.2rem;
  font-size: 8px;
  margin-top: 0.5rem;
}
